import { Component, OnInit } from "@angular/core";
import { CommonService } from "./services/common.service";
import { ZoomService } from "./services/zoom.service";
import { environment } from "src/environments/environment";
import { VersionService } from "./services/version.service";
import { AppTourService } from "./services/tour.service";
import { StorageService } from "./services/storage.service";
import { BaseComponent } from "./components/base.component";
import { AppViewModel } from "./models/view/app.viewmodel";
import { LogHandlerService } from "./services/log-handler.service";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"],
})
export class AppComponent
  extends BaseComponent<AppViewModel>
  implements OnInit
{
  title = "Unit Converter";
  constructor(
    logService: LogHandlerService,
    commonService: CommonService,
    private appTourService: AppTourService,
    private zoomService: ZoomService,
    private versionService: VersionService,
    private localStorageService: StorageService
  ) {
    super(commonService, logService);
    this.viewModel = new AppViewModel();
  }
  // /// on ngoninit we are fetching localstorage items and app version information.
  ngOnInit() {
    this.getFromStorageService();
    this.getAllVersionInfoAndCheckForUpdates();
  }

  ///get zoom factor from zoom buttons using zoom service
  get zoomFactor(): number {
    return this.zoomService.getZoomFactor();
  }
  // get all app version information from the server.
  async getAllVersionInfoAndCheckForUpdates() {
    try {
      const resp = await this.versionService.getAllVersionInfo();
      if (resp.isError) {
        this._commonService.showSweetAlertToast({
          title: "Error",
          text: resp.errorData.displayMessage,
          timer: 3000,
          position: "top-end",
        });
      } else {
        this.viewModel.versionInformation = resp.successData;
        const currentEnvironmentVersion = Number(
          environment.applicationVersion
        );
        const appLatestVersionFromApi = Number(
          this.viewModel.versionInformation.latestVersion
        );
        const minimumVersion = Number(
          this.viewModel.versionInformation.minimumVersion
        );

        if (currentEnvironmentVersion < appLatestVersionFromApi) {
          this.viewModel.updateAvailable = true;
        }
        // block flow if currenct version is below minimum version
        if (currentEnvironmentVersion < minimumVersion) {
          this.viewModel.stopApplicationFlow = false;
        }
      }
    } catch (error) {
      this._commonService.showSweetAlertToast({
        title: "Error",
        text: "Sorry We could not Fetch Version Information from Server.",
        timer: 3000,
      });
    }
  }

  ///// Update App if new version is found
  updateApp(): void {
    setTimeout(() => {
      window.open(this.viewModel.playstoreLink, "_blank");
    }, 500);
  }

  /// check localstorage if it is first install or not
  async getFromStorageService() {
    let checkIfFirstTimeInsTallation =
      await this.localStorageService.getFromStorage("first-install");
    this.viewModel.firstInstall = checkIfFirstTimeInsTallation;
    this.showTourTutorialOnAppInstall();
  }
  //// start tour tutorial based on condition if user is using the app first time
  showTourTutorialOnAppInstall() {
    if (this.viewModel.firstInstall === true) {
      return;
    } else {
      this.localStorageService.removeFromStorage("first-install"); //removing item from localstorage
      this.localStorageService.saveToStorage("first-install", true);
      this.appTourService.startTour(); //start tour
    }
  }
}
