import { Injectable } from "@angular/core";
import { ShepherdService } from "angular-shepherd";
import { BaseService } from "./base.service";

@Injectable({
  providedIn: "root",
})
export class AppTourService extends BaseService {
  constructor(public shepherdService: ShepherdService) {
    super();
  }

  startTour() {
    if (!this.shepherdService.isActive) {
      let defaultSteps = {
        classes: "shepherd-theme-arrows",
        scrollTo: false,
        cancelIcon: {
          enabled: true,
        },
      };
      this.shepherdService.modal = true;
      this.shepherdService.defaultStepOptions = defaultSteps;
      this.shepherdService.addSteps([
        {
          title: "Categories",
          text: `Click here to select different Category.`,
          attachTo: {
            element: ".common",
            on: "bottom",
          },
          classes:
            "shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text shepherd-step-highlight",

          buttons: [
            {
              action() {
                return this.next();
              },
              text: "Next",
            },
          ],
          id: "creating",
        },
        {
          title: "Zoom Functionality",
          text: `Click here to zoom in and out.`,
          attachTo: {
            element: ".zoom",
            on: "bottom",
          },
          classes:
            "shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text",

          buttons: [
            {
              action() {
                return this.back();
              },
              classes: "shepherd-button-secondary",
              text: "Back",
            },
            {
              action() {
                return this.next();
              },
              text: "Next",
            },
          ],
          id: "creating",
        },
        {
          title: "Search",
          text: `Search any Converter.`,
          attachTo: {
            element: ".search",
            on: "bottom",
          },
          classes:
            "shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text",

          buttons: [
            {
              action() {
                return this.back();
              },
              classes: "shepherd-button-secondary",
              text: "Back",
            },
            {
              action() {
                return this.next();
              },
              text: "Next",
            },
          ],
          id: "creating",
        },
        {
          title: "Settings",
          text: `You can change Themes and Decimal Value. `,
          attachTo: {
            element: ".bi-gear",
            on: "bottom",
          },
          classes:
            "shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text",

          buttons: [
            {
              action() {
                return this.back();
              },
              classes: "shepherd-button-secondary",
              text: "Back",
            },
            {
              action() {
                return this.next();
              },
              text: "Next",
            },
          ],
          id: "creating",
        },
        {
          title: "Choose Unit",
          text: `Select Any Unit to Convert. `,
          attachTo: {
            element: ".unit",
            on: "right",
          },
          classes:
            "shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text",

          buttons: [
            {
              action() {
                return this.back();
              },
              classes: "shepherd-button-secondary",
              text: "Back",
            },
            {
              action() {
                return this.next();
              },
              text: "Next",
            },
          ],
          id: "creating",
        },
        {
          title: "Enter Value",
          text: `Enter value here which you want to convert.`,
          attachTo: {
            element: ".timeinput",
            on: "bottom",
          },
          classes:
            "shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text",

          buttons: [
            {
              action() {
                return this.back();
              },
              classes: "shepherd-button-secondary",
              text: "Back",
            },
            {
              action() {
                return this.next();
              },
              text: "Next",
            },
          ],
          id: "creating",
        },
        {
          title: "Dropdown",
          text: `You can set target unit in this dropdown.`,
          attachTo: {
            element: ".drop-down",
            on: "bottom",
          },
          classes:
            "shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text",

          buttons: [
            {
              action() {
                return this.back();
              },
              classes: "shepherd-button-secondary",
              text: "Back",
            },
            {
              action() {
                return this.next();
              },
              text: "Next",
            },
          ],
          id: "creating",
        },
        {
          title: "Set Dropdown",
          text: `You can change dropdown value on click.`,
          attachTo: {
            element: ".drop-down-result",
            on: "bottom",
          },
          classes:
            "shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text",

          buttons: [
            {
              action() {
                return this.back();
              },
              classes: "shepherd-button-secondary",
              text: "Back",
            },
            {
              action() {
                return this.next();
              },
              text: "Next",
            },
          ],
          id: "creating",
        },
        {
          title: "Clear",
          text: `Click here to Clear all Values.`,
          attachTo: {
            element: ".clear",
            on: "bottom",
          },
          classes:
            "shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text",

          buttons: [
            {
              action() {
                return this.back();
              },
              classes: "shepherd-button-secondary",
              text: "Back",
            },
            {
              action() {
                return this.next();
              },
              text: "Finish",
            },
          ],
          id: "creating",
        },
      ]);
      this.shepherdService.modal = true;
      this.shepherdService.confirmCancel = false;
      this.shepherdService.start();
    }
  }
}
